    model() {
      let promises = [this.store.findAll('record', {
        reload: true
      }), this.store.findAll('contact', {
        reload: true
      }), this.store.findAll('dictionary', {
        reload: true
      })];

      let meta = A([EmberObject.create({
        type: 'record',
        list: 'records',
        title: 'Metadata Records',
        icon: 'file-o'
      }), EmberObject.create({
        type: 'contact',
        list: 'contacts',
        title: 'Contacts',
        icon: 'users'
      }), EmberObject.create({
        type: 'dictionary',
        list: 'dictionaries',
        title: 'Dictionaries',
        icon: 'book'
      })]);

      let idx = 0;

      let mapFn = function mapFn(item) {

        meta[idx].set('listId', guidFor(item));
        item.set('meta', meta[idx]);
        idx = ++idx;

        return item;
      };

      return RSVP.map(promises, mapFn);
    },

